
import React from "react";
import {Heading } from '@chakra-ui/react';
import {VStack}from '@chakra-ui/react';
import { HStack , StackDivider,Spacer,Badge} from '@chakra-ui/react';
import Detail from './Detail';
class Home extends React.Component{
    constructor(){
        super()
        this.state={
            data:[
                {id:1,name:"Ayush Singh",email:"tejasvasingh.83@gmail.com",date:"12-30-2",number:9161463059,timing:"12",interviewer:"snehalata"},
                {id:2,name:"Ritu Singh",email:"ritu.83@gmail.com",date:"34-30-2",number:9161463059,timing:"9",interviewer:"karan sharma"},
            ],
        }
    }
    render(){
        const {data}=this.state;
        const{toggle}=this.state;
        return(
            <VStack p={8}>
                <Heading mg='8' my='5' fontWeight='extrabold' size='2xl' bgGradient="linear(to-r,pink.500,pink.300,blue.500)" bgClip='text'>
                    Candidate List
                </Heading>
                <VStack divider={<StackDivider/>} borderColor='grey.100' borderWidth='2px'  borderRadius='lg' width="100%" maxW={{base:'90vw' ,sm:'80vw' ,lg:'50vw' ,xl:'40vw'}} alignItems="st">
                    {
                        data.map((task,id)=>(
                           <Detail key={id} name={task.name} email={task.email} number={task.number} date={task.date} time={task.timing} intervew={task.interviewer} /> 
                        ))
                    }
                </VStack>
            </VStack>
            
        )
    }
}
export default Home;